@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Quicksand:300,400,500,700&display=swap");
body {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  color: #383a4d;
  background-color: #fafbfd; }

.page {
  width: 70%;
  max-width: 1100px;
  min-width: 300px;
  margin: 0 auto;
  padding: 100px 20px 50px; }

.logoContainer {
  height: 36px;
  display: flex;
  margin-right: 20px;
  flex-grow: 1;
  cursor: pointer; }
  @media only screen and (max-width: 768px) {
    .logoContainer {
      margin-right: 0px; } }

.logoText {
  height: 36px;
  font-family: "Quicksand", sans-serif;
  font-weight: 800;
  font-size: 30px; }

.activeNav {
  color: #e61f40 !important; }

.logo {
  background-image: url("/static/logo.png");
  background-position: 50%;
  background-size: cover;
  display: block;
  width: 36px;
  height: 36px;
  margin-right: 10px; }

a,
a:link,
a:visited {
  color: #383a4d;
  text-decoration: none; }

a:hover,
a:active {
  color: #e61f40; }

.hero {
  position: absolute;
  top: 30px;
  left: 0;
  bottom: 0;
  right: 0;
  height: 750px;
  background-image: url("/static/logo.png"), url("/static/waves.svg");
  background-position: 50%, 50%;
  background-repeat: no-repeat, no-repeat;
  background-size: 278px 278px, 750px 750px; }

.download {
  background-image: url("/static/appstore.svg");
  width: 160px;
  height: 63px;
  background-repeat: no-repeat;
  background-size: contain; }

.footer {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  color: #383a4d;
  font-size: 1rem;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: absolute;
  max-width: 278px;
  top: 550px;
  left: 50%;
  z-index: 3;
  transform: translateX(-50%); }

.title {
  font-family: "Quicksand", sans-serif;
  font-weight: 800;
  font-size: 80px;
  position: absolute;
  top: 80px;
  left: 0;
  width: 100%;
  text-align: center; }

.subtitle {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  color: #383a4d;
  font-size: 1rem;
  font-weight: 400;
  margin-bottom: 20px; }

.header {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  background-color: #fff;
  height: 60px; }

.nav {
  width: 100%;
  max-width: 1100px;
  min-width: 300px;
  margin: 0 auto;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .nav > a {
    margin-left: 40px; }
    @media only screen and (max-width: 768px) {
      .nav > a {
        margin-left: 20px; } }
